.ss_table {
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  height: fit-content;
  margin-bottom: 1rem;
}
.ss_table_header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 6.5rem;
  padding-block-start: 0.3rem;
}
.ss_table_header_inner {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #cacaca;
  border-right: none;
  height: 100%;
}
.ss_table_header_inner_height {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #cacaca;
  border-right: none;
  height: 2rem;
}
.ss_table_header_inner_fr_center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.ss_table_header_h6 {
  color: var(--t-head-color);
  font-size: var(--font-14);
  font-weight: var(--font-5);
  line-height: var(--line-18);
  margin: 0;
  padding: 0.5rem;
  text-align: center;
}
.ss_table_header_inner_fr {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #cacaca;
  border-right: none;
  height: 100%;
}
.ss_table_header_inner_fr_inner {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ss_table_header_inner_fr_inner_h6,
.ss_table_header_inner_fr_inner_h6_right {
  width: 100%;
  color: var(--t-head-color);
  font-size: var(--font-14);
  font-weight: var(--font-5);
  line-height: var(--line-18);
  margin: 0;
  padding: 0.5rem;
  border: 1px solid #cacaca;
  border-left: none;
  border-bottom: none;
  text-align: center;
}
.ss_table_header_inner_fr_inner_h6_right {
  border-right: none;
}
.ss_table_minority {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #cacaca;
}
.ss_table_minority_inner {
  border-right: 1px solid #cacaca;
}
/* // for fees statistics  */
.fs_table_header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-top: 0.3rem;
  padding-bottom: 0.8rem;
  height: fit-content;
}
.fs_table_col {
  border-right: 1px solid #cacaca;
  border-bottom: 1px solid #cacaca;
}
.fs_table_header_inner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  border: 1px solid #cacaca;
}
.fs_table_header_inner_h6 {
  color: var(--t-head-color);
  font-size: var(--font-14);
  font-weight: var(--font-5);
  line-height: var(--line-18);
  margin: 0;
  padding: 0.5rem;
  text-align: center;
}
.fs_table_col_inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
/* .osc_container {
} */
.osc_card {
  width: 32.5%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  padding: 1rem;
  background-color: var(--bg-color-white);
  border: 0.4px solid var(--border-color);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: var(--radius-12);
  cursor: pointer;
}
.osc_card > img {
  height: 1.5rem;
  width: 1.5rem;
}
.osc_card_inner > h6 {
  color: var(--t-head-color);
  font-size: var(--font-13);
  font-weight: var(--font-5);
  line-height: var(--line-15);
  margin: 0;
  margin-bottom: 0.4rem;
}
.osc_card_inner > p {
  color: var(--t-head-color);
  font-size: var(--font-11);
  font-weight: var(--font-4);
  line-height: var(--line-15);
  margin: 0;
}
.osc_container_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2%;
  margin-bottom: 1rem;
}
/* // for student enrollment related  */
.set_main {
  width: 100%;
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  height: fit-content;
  margin-bottom: 1rem;
  border: 1px solid #121212;
}
.set_section {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0;
  align-items: start;
}
.sets_c {
  display: grid;
  place-items: center;
  border-right: 1px solid #121212;
}
.sets_c:last-child {
  border-right: none;
}
.setsc_r {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0;
  width: 100%;
}
.setscn_u {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  height: 89px;
}

.setscn_u:last-child {
  border-bottom: 1px solid #121212;
}
.setsc_n {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  width: 100%;
}

.setscr_f {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  border-right: 1px solid #121212;
  border-bottom: 1px solid #121212;
  /* height: 89px; */
}

.setscr_f:last-child {
  border-right: none;
}
.setscr_h {
  width: 3000px;
  display: grid;
  /* grid-template-columns: repeat(18, 1fr); */
  grid-template-columns: 80px 80px 80px 80px 80px 80px 80px 80px 80px 80px 80px 80px 80px 80px 500px 80px 80px 140px;
  gap: 0;
  height: 89px;
}
.setscrf_y {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  border-right: 1px solid #121212;
  border-bottom: 1px solid #121212;
  height: 89px;
}

.setscrf_y:last-child {
  border-right: none;
}
.setscrh_g {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0;
  width: 100%;
  height: 100%;
}
.setscrhg_z {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  border-right: 1px solid #121212;
  height: 100%;
}
.setscrhg_z:last-child {
  border-right: none;
}
.setscrh_n {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0;
  width: 100%;
  height: 100%;
}
.setscrhg_t {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0;
  width: 100%;
  height: 100%;
}
.setscrhgt_p {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  border-right: 1px solid #121212;
  border-bottom: 1px solid #121212;
  height: 70px;
}

.setscrhgt_p:last-child {
  border-right: none;
}
.set_dp_heading {
  font-size: var(--font-16);
  color: var(--t-head-color);
  font-weight: var(--font-6);
  line-height: var(--line-18);
  margin: 0;
  display: grid;
  place-items: center;
  padding-block: 0.3rem;
}
.set_heading {
  font-size: var(--font-14);
  color: var(--t-head-color);
  font-weight: var(--font-5);
  line-height: var(--line-18);
  margin: 0;
}
.setscrhgz_n {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  width: 100%;
  border-bottom: 1px solid #121212;
}
.setscrhgz_n {
  border-right: 1px solid #121212;
}

/* // new ui of enrollment after first one is wrong  */
.setscn_y {
  display: grid;
  place-items: center;
  border-top: 1px solid #121212;
  border-right: 1px solid #121212;
  height: 40px;
}
/* .setscn_y:last-child {
  border-bottom: 1px solid #121212;
} */
.setscn_yt {
  display: grid;
  place-items: center;
  border-right: 1px solid #121212;
  border-bottom: 1px solid #121212;
  height: 40px;
  width: 100%;
}
.setscn_yt:last-child {
  border-bottom: none;
}
.setscr_op {
  display: grid;
  place-items: center;
  height: 100%;
}
.settt_y {
  display: grid;
  place-items: center;
  border-bottom: 1px solid #121212;
  height: 40px;
}
.settt_y:last-child {
  border-bottom: none;
}
